*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
}

button {
  cursor: pointer;
  color: inherit;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  font-weight: 400;
}

img {
  max-width: 100%;
  display: block;
}

input,
textarea {
  padding: 10px;
}

@font-face {
  font-family: "OpenSans";
  src: local("OpenSans-Bold"),url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "OpenSans";
  src: local("OpenSans-ExtraBold"),url("../fonts/OpenSans-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "OpenSans";
  src: local("OpenSans-Light"),url("../fonts/OpenSans-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "OpenSans";
  src: local("OpenSans-Regular"),url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "OpenSans";
  src: local("OpenSans-SemiBold"),url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.--dir-col {
  flex-direction: column;
}

img {
  object-fit: cover;
  height: 100%;
}

html,
body {
  color: var(--body-color);
  background-color: var(--body-bcolor);
  font-family: "OpenSans", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.42;
  transition: all 0.5s ease 0s;
}

svg {
  fill: var(--body-color);
  stroke: var(--body-color);
  transition: all 0.3s ease 0s;
}

a,
.header__contacts-title {
  color: inherit;
  transition: all 0.3s ease 0s;
}

a:not(.link-btn):hover {
  color: var(--body-hcolor);
}

button {
  background-color: inherit;
}

h1,
h2 {
  font-size: calc(30px + 20 * ((100vw - 320px) / (1920 - 320)));
  font-weight: 900;
  line-height: 1.2;
}

h3 {
  font-size: 25px;
  font-weight: 900;
  line-height: 1.2;
}

h4 {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.4;
}

body.lock {
  overflow: hidden;
}

._wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;
}

.header.open ~ .overlay {
  visibility: visible;
  opacity: 1;
}

.overlay {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 5;
  background-color: rgba(0,0,0,0.5);
  transition: all 0.5s ease 0s;
}

.main {
  flex: 1 1 auto;
}

.wrapper {
  margin-top: 100px;
}

.container {
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
  position: relative;
}

.container--1862 {
  width: 100%;
  max-width: 1882px;
  padding: 0 10px;
  margin: 0 auto;
  position: relative;
}

.container--1432 {
  width: 100%;
  max-width: 1452px;
  padding: 0 10px;
  margin: 0 auto;
  position: relative;
}

.container--1300 {
  width: 100%;
  max-width: 1320px;
  padding: 0 10px;
  margin: 0 auto;
  position: relative;
}

.link-dashed {
  width: fit-content;
  border-bottom: 1px dashed var(--body-color);
}

.link-dashed:hover {
  border-bottom: 1px dashed var(--body-hcolor);
}

.link-btn {
  border-radius: 30px;
}

.link-btn:hover {
  box-shadow: 0px 5px 20px 2px var(--body-bscolor);
}

.link-btn-white {
  padding: 20px 60px;
}

.link-btn-blue {
  color: var(--body-bcolor);
  background-color: #1a97d5;
}

.link-btn-details {
  padding: 15px 50px;
}

.leisure__slide,
.main-slider__slide .main-slider__slide-img,
.gallery__slider-slide .gallery__slider-img {
  background: url("../img/loading.gif") center/50px no-repeat;
}

