.event>.container--1300 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.event>.container--1300>*:not(:last-child) {
  margin-bottom: 80px;
}

.event__title {
  text-align: center;
  max-width: 745px;
}

.event__content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px;
  width: 90%;
}

.event__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.event__date-item {
  font-size: 14px;
  color: #1b98d3;
  margin-bottom: 17px;
}

.event__title-item {
  margin-bottom: 24px;
}

.event__text-item {
  flex-grow: 1;
  font-weight: 700;
  margin-bottom: 24px;
}

.faq>.container--1300 {
  text-align: center;
}

.faq__title {
  margin-bottom: 50px;
}

.faq__content {
  margin-bottom: 100px;
}

.faq__spoiler {
  text-align: start;
}

.faq__question>.close {
  position: absolute;
  right: 20px;
  transform: rotate(45deg);
}

.faq__question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px;
  border-top: 1px solid #c6d6dd;
  border-bottom: 1px solid #c6d6dd;
  cursor: pointer;
  transition: all .3s ease 0s;
}

.faq__question:hover {
  background-color: var(--body-hbcolor);
}

.faq__question-title {
  padding-right: 15px;
  user-select: none;
}

.faq__answer {
  overflow: hidden;
  height: 0;
  margin: 0 32px;
  transition: all .3s ease 0s;
}

.faq__question.open ~ .faq__answer {
  overflow: visible;
  height: auto;
  margin: 32px;
}

.faq__question.open>.close {
  transform: rotate(90deg);
  fill: #75baff;
}

.faq__answer-text {
  margin-bottom: 32px;
}

.faq__answer-detail {
  border-bottom: 1px dashed #000;
}

.gallery>.container--1432 {
  max-width: 868px;
}

.gallery__slider {
  overflow: hidden;
  opacity: 0;
  width: 100%;
  height: 0;
  transition: all .3s ease 0s;
}

.gallery__slider.active1 {
  overflow: visible;
  opacity: 1;
  height: 382px;
}

.tabs__link--gallery.active1 {
  border-bottom: 2px solid #62b3d9;
}

.gallery__slider-wrapper {
  user-select: none;
}

.gallery__slider-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 382px;
  cursor: pointer;
}

.gallery__img-zoom {
  position: absolute;
  width: 100px;
  height: 100px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease 0s;
}

.gallery__img-play {
  position: absolute;
  width: 84px;
  height: 125px;
}

.gallery__slider-slide:hover>.gallery__img-zoom {
  visibility: visible;
  opacity: 1;
}

.gallery__slider-button-prev.swiper-button-prev {
  left: 0;
}

.gallery__slider-button-next.swiper-button-next {
  right: 0;
}

.gallery__slider-button-prev.swiper-button-prev,
.gallery__slider-button-next.swiper-button-next {
  top: unset;
  bottom: 0;
  transition: all 0.3s ease 0s;
  width: 80px;
  height: 100%;
}

.gallery__slider-button-prev.swiper-button-prev.swiper-button-disabled,
.gallery__slider-button-next.swiper-button-next.swiper-button-disabled {
  cursor: not-allowed;
  pointer-events: visible;
}

.gallery__slider-button-prev.swiper-button-prev::after,
.gallery__slider-button-next.swiper-button-next::after {
  font-size: 40px;
  color: var(--body-bcolor);
  transition: all 0.3s ease 0s;
}

.gallery__slider-button-prev:hover,
.gallery__slider-button-next:hover {
  background-color: rgba(0,0,0,0.2);
}

.leisure>.container--1432 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.leisure__title {
  text-align: center;
  max-width: 760px;
  margin-bottom: 50px;
}

.tabs {
  width: 100%;
  margin-bottom: 40px;
}

.tabs__list {
  display: flex;
  justify-content: space-between;
}

.tabs__link {
  display: block;
  padding: 12px 14px;
}

.tabs__link.active {
  border-bottom: 2px solid #62b3d9;
}

.leisure__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(444px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(fit-content, 1fr));
  gap: 20px;
  overflow: hidden;
  opacity: 0;
  height: 0;
  transition: all .3s ease 0s;
}

.leisure__list.active {
  overflow: visible;
  opacity: 1;
  height: auto;
}

.leisure__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 1px 4px 0 var(--body-bscolor1);
}

.leisure__slider {
  width: 100%;
  height: 330px;
}

.leisure__slider-button-prev.swiper-button-prev {
  left: 28px;
}

.leisure__slider-button-next.swiper-button-next {
  left: 80px;
}

.leisure__slider-button-prev.swiper-button-prev,
.leisure__slider-button-next.swiper-button-next {
  top: unset;
  bottom: 0;
  background-color: var(--body-hbcolor);
  width: 52px;
  height: 52px;
}

.leisure-slider-stick {
  position: absolute;
  bottom: 7px;
  left: 80px;
  z-index: 11;
  height: 35px;
  width: 1px;
  background-color: #DCDCDC;
}

.leisure__slider-button-prev.swiper-button-prev::after,
.leisure__slider-button-next.swiper-button-next::after {
  font-size: 15px;
  font-weight: bold;
  color: var(--body-color);
  transition: all 0.3s ease 0s;
}

.leisure__slider-button-prev:hover.swiper-button-prev::after,
.leisure__slider-button-next:hover.swiper-button-next::after {
  color: var(--body-hcolor);
}

.leisure__item-content {
  flex-grow: 1;
  padding: 25px 0 0 29px;
}

.leisure__inner {
  display: flex;
  justify-content: space-between;
  margin-bottom: 22px;
}

.leisure__inner-title {
  flex-grow: 1;
  max-width: 220px;
  height: fit-content;
  font-size: 18px;
  font-weight: 700;
  padding-right: 10px;
}

.leisure__inner-price {
  color: var(--body-bcolor);
  text-align: center;
  height: 36px;
  background-color: #1b98d3;
  padding: 8px;
}

.leisure__item-text {
  margin-bottom: 22px;
  padding-right: 10px;
}

.leisure__item-detail {
  margin-bottom: 30px;
}

.main-slider {
  height: 100vh;
}

.main-slider__slide-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}

.main-slider__slide-gradient {
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(90deg, rgba(0,0,0,0.6), rgba(255,255,255,0));
  width: 40%;
  height: 100vh;
}

.main-slider__slide-wrapper {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 50vh;
  top: 30%;
  gap: 50px;
}

.main-slider__slide-wrapper>.logo-mobile {
  min-height: 186px;
}

.main-slider__slide-title {
  max-width: 677px;
}

.main-slider__container {
  height: 100%;
}

.main-slider .swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 4.5vh;
}

.main-slider .swiper-pagination .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background: var(--body-bcolor);
  margin: 0 7.5px;
  opacity: 1;
}

.main-slider .swiper-pagination .swiper-pagination-bullet-active {
  position: relative;
  width: 18px;
  height: 18px;
  background: var(--body-bcolor);
}

.main-slider .swiper-pagination .swiper-pagination-bullet-active div {
  position: absolute;
  top: 20%;
  left: 23%;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--body-color);
}

.main-slider__button-prev.swiper-button-prev {
  left: calc(50% - 133px);
}

.main-slider__button-next.swiper-button-next {
  right: calc(50% - 133px);
}

.main-slider__button-prev.swiper-button-prev,
.main-slider__button-next.swiper-button-next {
  top: 95vh;
  background-color: var(--body-bcolor);
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.main-slider__button-prev.swiper-button-prev::after,
.main-slider__button-next.swiper-button-next::after {
  font-size: 16px;
  font-weight: bold;
  color: var(--body-color);
}

.footer {
  padding: 20px 0;
  color: #585858;
  font-size: 14px;
}

.footer>.container--1862 {
  display: flex;
  align-items: center;
}

.footer__list-copy,
.footer__list-dev {
  flex: 1 0 20%;
}

.footer__list-dev {
  justify-content: flex-end;
}

.footer__list-copy>*:not(:last-child),
.footer__list-dev>*:not(:last-child) {
