}

.footer__item-nav {
  margin: 5px 0;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 6;
  width: 100%;
  background-color: var(--body-bcolor);
  box-shadow: 0 1px 4px 0 rgba(0,0,0,0.1);
}

.header .container--1862 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
}

.logo>.logo-desktop {
  height: 90px;
}

.logo>.logo-mobile {
  display: none;
}

.logo__title {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
}

.logo>*:last-child {
  margin-left: 12px;
}

.burger-menu {
  display: flex;
  align-items: center;
}

.burger-menu>*:last-child {
  margin-left: 15px;
}

.burger {
  display: flex;
  flex-direction: column;
  align-items: self-end;
  width: 30px;
  height: 15px;
}

.bar {
  display: block;
  position: relative;
  background-color: var(--body-color);
  transition: all .3s ease 0s;
  height: 1px;
}

.bar:nth-child(1) {
  top: 0;
  width: 100%;
}

.bar:nth-child(2) {
  top: 50%;
  width: 75%;
}

.bar:nth-child(3) {
  top: 100%;
  width: 50%;
}

.burger-menu:hover>.burger>.bar {
  width: 100%;
}

.burger-menu--open>.burger>.bar {
  top: 7px;
}

.burger-menu--open>.burger>.bar:nth-child(1) {
  opacity: 0;
}

.burger-menu--open>.burger>.bar:nth-child(2) {
  width: 100%;
  transform: translateY(-50%) rotate(45deg);
}

.burger-menu--open>.burger>.bar:nth-child(3) {
  width: 100%;
  transform: translateY(-50%) rotate(-45deg);
}

.burger-menu--open2 {
  width: 30px;
  height: 30px;
  transform: rotate(90deg);
  background-repeat: no-repeat;
  background-position: center;
  transition: all .1s ease 0s;
}

.burger-menu__title {
  font-size: 25px;
  font-weight: 900;
}

.header__menu-list {
  display: flex;
}

.header__menu-link {
  display: block;
  padding: 40px 14px;
  font-weight: 700;
}

.header__menu-link:hover {
  background-color: var(--body-hbcolor);
  color: var(--body-hcolor);
}

.header__menu-item {
  position: relative;
}

.header__menu-item--services {
  display: none;
}

.header__menu-list>.header__menu-item:last-child>.pattern {
  display: none;
}

.pattern {
  position: absolute;
  top: 46%;
  right: -4px;
  z-index: 1;
  fill: none;
  stroke: #d3d3d3;
  width: 9px;
  height: 9px;
}

.header__contacts {
  display: flex;
}

.header__contacts>*:not(:last-child),
.header__contacts-link>*:not(:last-child) {
  margin-right: 12px;
}

.header__contacts-link {
  display: flex;
  align-items: center;
}

.header__contacts-link--btn {
  border: 1px solid var(--body-hbcolor);
  border-radius: 50%;
  padding: 8px;
}

.header__socials {
  display: flex;
  align-items: center;
}

.header__socials>* {
  padding: 25px 13px;
}

.header__contacts-link:hover>*,
.header__social-item:hover>*,
.main-menu__btn:hover>* {
  stroke: #0d6690;
  color: #0d6690;
}

.main-menu {
  overflow: auto;
  position: fixed;
  top: 95px;
  left: -100%;
  z-index: 6;
  width: 610px;
  height: 90%;
  background-color: var(--body-bcolor);
  padding: 20px 0 0 130px;
  transition: left 0.3s ease 0s;
}

.main-menu.open,
.header.open>.container--1862>.main-menu,
.header.open>.container--1862>.header__menu {
  left: 0;
}

.main-menu__complex {
  position: relative;
  border: 2px solid #ddd;
  padding: 20px 50px;
  margin-right: 100px;
  line-height: 1.8;
}

.main-menu__item--logo {
  position: absolute;
  top: 50%;
  right: 78%;
  padding: 10px;
  background-color: var(--body-bcolor);
  transform: translateY(-50%) rotate(-90deg);
}

.main-menu__subtitle,
.main-menu__title {
  text-align: right;
  font-weight: 900;
  color: #c6c6c6;
}

.main-menu__subtitle {
  font-size: 24px;
  line-height: 26px;
}

.main-menu__title {
  font-size: 44px;
  line-height: 46px;
}

.main-menu__item--price {
  position: absolute;
  bottom: 205px;
  right: -32px;
  padding: 8px;
  background-color: #c6c6c6;
  color: var(--body-bcolor);
}

.main-menu__link {
  display: block;
  font-size: 20px;
  font-weight: 900;
  padding-right: 15px;
}

.main-menu__list {
  margin: 10px 0 0 50px;
  line-height: 1.8;
}

.main-menu__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0 20px 50px;
  max-width: 230px;
  padding: 13px;
  border: 1px solid #c6c6c6;
}

.main-menu__btn:hover {
  border: 1px solid #0d6690;
}

.main-menu__btn>*:not(:last-child) {
  margin-right: 30px;
}

.main-menu::-webkit-scrollbar,
.main-rooms::-webkit-scrollbar,
.main-cafe::-webkit-scrollbar {
  display: none;
}

.main-menu__item--rooms:hover .main-rooms,
.main-menu__item--cafe:hover .main-cafe {
  opacity: 1;
  visibility: visible;
}

.main-rooms {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 10px 20px;
  left: 610px;
  opacity: 0;
  visibility: hidden;
  width: 65%;
  padding: 20px 40px 20px 0;
  transition: opacity .5s .15s,visibility .5s .15s;
}

.main-rooms__item {
  transition: all 0.3s ease 0s;
}

.main-rooms__img {
  width: 100%;
  height: 166px;
}

.main-rooms__content {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.main-rooms__title {
  font-size: 14px;
  font-weight: 600;
  width: 45%;
}

.main-rooms__price {
  background-color: #1b98d3;
  font-size: 12px;
  font-weight: 600;
  text-align: center;
  color: var(--body-bcolor);
  padding: 5px 10px;
  height: 31px;
}

.main-cafe {
  grid-template-rows: 210px;
}

.main-cafe__title {
  margin-top: 10px;
}

